.App {
  background-color: #282c34;
  font-family: Sofia Pro,serif;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-height: 800px;
}

.App-logo {
  height: 60px;
  width: 60px;
  display: block;
  margin: 0 auto;
  margin-bottom: 0;
  cursor: pointer;
}


.App-header {
  height: 70px;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  align-items: center;
  font-size: calc(10px + 2vmin);
  background-color:rgba(16,20,27,.8);
}

.App-link {
  color: #61dafb;
}

.ButtonContainer
{
  right: 0;
  position: absolute;
  margin-right: 30px;
  position:absolute;
}

main
{
  position: absolute;
  top: 70px;
  width: 100%;
}

 body
 {
  position: absolute;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
 }



